"use client";

import React, { useState } from "react";
import { predictCreditRisk, predictCreditRiskCsv } from "@/src/services/creditRiskAPI";
import { Button } from "@/src/components/ui/button";
import { Input } from "@/src/components/ui/input";
import { Select, SelectTrigger, SelectValue, SelectContent, SelectItem } from "@/src/components/ui/select";

interface PredictionResult {
  Predicted_Risk: string;
  Low_Risk_Probability: number;
  High_Risk_Probability: number;
}

export default function CreditCalculator() {
  const [inputMethod, setInputMethod] = useState<"manual" | "csv">("manual");
  const [file, setFile] = useState<File | null>(null);
  const [loading, setLoading] = useState(false);
  const [result, setResult] = useState<PredictionResult | PredictionResult[] | null>(null);

  const [formData, setFormData] = useState({
    person_age: 25,
    person_income: 300,
    person_home_ownership: "RENT",
    person_emp_length: 12,
    loan_intent: "START BUSINESS",
    loan_grade: "B",
    loan_amnt: 200,
    loan_int_rate: 0.1,
    loan_percent_income: 0.5,
    cb_person_default_on_file: "N",
    cb_person_cred_hist_length: 5,
  });

  const handleChange = (key: string, value: any) => {
    setFormData({ ...formData, [key]: value });
  };

  const handlePredict = async () => {
    try {
      setLoading(true);
      if (inputMethod === "manual") {
        const res = await predictCreditRisk(formData);
        setResult(res);
      } else if (file) {
        const res = await predictCreditRiskCsv(file);
        setResult(res);
      }
    } catch (error) {
      alert("Prediction failed. Check your backend.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="p-8 bg-gray-50 min-h-screen">
      <h1 className="text-3xl font-bold text-center mb-6">üí≥ Credit Risk Scoring</h1>

      <div className="flex justify-center mb-6 space-x-4">
        <Button
          variant={inputMethod === "manual" ? "default" : "outline"}
          onClick={() => setInputMethod("manual")}
        >
          Manual Input
        </Button>
        <Button
          variant={inputMethod === "csv" ? "default" : "outline"}
          onClick={() => setInputMethod("csv")}
        >
          Upload CSV
        </Button>
      </div>

      {/* MANUAL INPUT FORM */}
      {inputMethod === "manual" && (
        <div className="max-w-xl mx-auto p-6 bg-white shadow rounded-xl space-y-4">
          <div className="grid grid-cols-2 gap-4">
            <Input
              type="number"
              value={formData.person_age}
              onChange={(e) => handleChange("person_age", +e.target.value)}
              placeholder="Age"
            />
            <Input
              type="number"
              value={formData.person_income}
              onChange={(e) => handleChange("person_income", +e.target.value)}
              placeholder="Annual Income"
            />

            <Select
              defaultValue={formData.person_home_ownership}
              onValueChange={(v) => handleChange("person_home_ownership", v)}
            >
              <SelectTrigger><SelectValue placeholder="Home Ownership" /></SelectTrigger>
              <SelectContent>
                {["RENT", "FRIEND", "OWNER", "PARRENT", "COMPANY", "SIBLING"].map((opt) => (
                  <SelectItem key={opt} value={opt}>{opt}</SelectItem>
                ))}
              </SelectContent>
            </Select>

            <Input
              type="number"
              value={formData.person_emp_length}
              onChange={(e) => handleChange("person_emp_length", +e.target.value)}
              placeholder="Employment Length (Months)"
            />

            <Select
              defaultValue={formData.loan_intent}
              onValueChange={(v) => handleChange("loan_intent", v)}
            >
              <SelectTrigger><SelectValue placeholder="Loan Intent" /></SelectTrigger>
              <SelectContent>
                {["START BUSINESS", "EXPAND BUSINESS", "HOME REPAIR", "TRAVEL", "HEALTH"].map((opt) => (
                  <SelectItem key={opt} value={opt}>{opt}</SelectItem>
                ))}
              </SelectContent>
            </Select>

            <Select
              defaultValue={formData.loan_grade}
              onValueChange={(v) => handleChange("loan_grade", v)}
            >
              <SelectTrigger><SelectValue placeholder="Loan Grade" /></SelectTrigger>
              <SelectContent>
                {["A", "B", "C", "D", "E", "F"].map((opt) => (
                  <SelectItem key={opt} value={opt}>{opt}</SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>

          <Button onClick={handlePredict} disabled={loading} className="w-full mt-4">
            {loading ? "Predicting..." : "üîÆ Predict Credit Risk"}
          </Button>
        </div>
      )}

      {/* CSV INPUT */}
      {inputMethod === "csv" && (
        <div className="max-w-md mx-auto bg-white p-6 shadow rounded-xl text-center">
          <input
            type="file"
            accept=".csv"
            onChange={(e) => setFile(e.target.files?.[0] || null)}
            className="mb-3"
          />
          <Button onClick={handlePredict} disabled={!file || loading} className="w-full">
            {loading ? "Predicting..." : "üìä Predict from CSV"}
          </Button>
        </div>
      )}

      {/* RESULT */}
      {result && (
        <div className="mt-10 text-center bg-white p-6 rounded-xl shadow max-w-lg mx-auto">
          {Array.isArray(result) ? (
            <>
              <h2 className="font-semibold mb-3">üìà Prediction Results (CSV)</h2>
              <table className="min-w-full border">
                <thead>
                  <tr className="bg-gray-100">
                    <th className="border px-2 py-1">#</th>
                    <th className="border px-2 py-1">Risk</th>
                    <th className="border px-2 py-1">High Risk Probability</th>
                  </tr>
                </thead>
                <tbody>
                  {result.map((r: any, i: number) => (
                    <tr key={i}>
                      <td className="border px-2 py-1">{i + 1}</td>
                      <td className={`border px-2 py-1 ${r.Predicted_Risk === "High Risk" ? "text-red-600 font-semibold" : "text-green-600"}`}>
                        {r.Predicted_Risk}
                      </td>
                      <td className="border px-2 py-1">{(r.High_Risk_Probability * 100).toFixed(1)}%</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </>
          ) : (
            <>
              <h2 className="font-semibold text-lg">üß† Prediction Result</h2>
              <p className={`text-2xl font-bold ${result.Predicted_Risk === "High Risk" ? "text-red-600" : "text-green-600"}`}>
                {result.Predicted_Risk}
              </p>
              <p className="text-gray-600">
                High Risk Probability: {(result.High_Risk_Probability * 100).toFixed(1)}%
              </p>
            </>
          )}
        </div>
      )}
    </div>
  );
}
